.swiper-wrapper {
  .swiper {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .swiper-slide {
    background-position: center;
    background-size: cover;
    width: 30rem;
    height: 40rem;
    background-color: #fff;
    border-radius: 1.5rem;
    overflow: hidden;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
  }
}
